.welcome2 {

    .container {
        &.full {
            background-color: #f9f9f9;
            border-bottom: 1px solid #dadada;
        }
    }

    .border {
        border: 1px solid #dadada;

        .item {
            border-bottom: 0;
        }
    }

    .item {
        float: left;
        width: 316px;
        height: 128px;
        overflow: hidden;
        border-left: 1px solid #dadada;

        &:first-child {
            border-left: 0;
        }
    }
}

